import { Component, Directive, EventEmitter, Input, Output } from '@angular/core';
import { BbTemplate } from '@backbase/foundation-ang/core';
import { NotificationItem } from '@backbase/notifications-common-ang';

@Component({
  selector: 'bb-notification-details',
  templateUrl: './notification-details.component.html',
})
export class NotificationDetailsComponent {
  /**
   * Notification item data.
   */
  @Input() notification!: NotificationItem;

  /**
   * Format to display date in past
   */
  @Input() dateFormat: string | undefined;

  /**
   * Event to navigate back to notifications list.
   */
  @Output() navigateBack = new EventEmitter<void>();

  /**
   * Event to delete notification.
   */
  @Output() delete = new EventEmitter<void>();

  /**
   * Method to emit delete notification event.
   */
  deleteNotification() {
    this.delete.emit();
  }
}

@Directive({
  selector: '[bbNotificationDetailsCustomizable]',
})
export class NotificationDetailsCustomizableDirective extends BbTemplate<NotificationDetailsComponent, void> {}
